.container-card {
  box-shadow: 0 3px 12px rgba(0, 0, 0, 0.06);
  padding: 2rem;
  max-width: 800px;
}
@media (max-width: 1200px) {
  .container-card {
    max-width: 100% !important;
  }
}
@media (max-width: 600px) {
  .container-card {
    padding: 1rem;
    max-width: 100vw !important;
  }
  .container-card .input-group {
    flex-direction: column;
    align-items: stretch;
  }
  .container-card .input-group button {
    width: 100%;
  }
  .container-card .actions {
    flex-direction: column;
    align-items: center;
  }
  .container-card .actions button {
    width: 100%;
  }
}
.container-card .input-group {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1rem;
  justify-content: center;
}
.container-card .input-group .barcode {
  min-width: 270px;
  max-width: 100%;
  padding: 0.5rem;
}
.container-card .input-group .barcode:focus {
  border: 2px solid #10b981;
}
.container-card .input-group button {
  min-width: 2.5rem;
  height: 2.5rem;
  white-space: nowrap;
}
.container-card .p-invalid {
  border: 1px solid #f44336 !important;
}
.container-card .empty-info {
  margin-top: 2rem;
}

::ng-deep .fullscreen-dialog {
  width: 100vw !important;
  max-width: 100vw !important;
  height: 100vh !important;
  top: 0 !important;
  left: 0 !important;
  margin: 0;
  border-radius: 0;
}
::ng-deep .fullscreen-dialog .p-dialog-content {
  height: calc(100vh - 4rem);
  padding: 0;
  overflow: hidden;
}

.scanner-container {
  width: 100%;
  height: 100%;
  position: relative;
  background-color: #000;
}
.scanner-container .video-feed {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.scanner-container .overlay {
  position: absolute;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  color: #fff;
  width: 100%;
  text-align: center;
  padding: 1rem;
  font-size: 1.2rem;
}
